import React, { useCallback, useEffect, useRef } from 'react';
import { Route, useHistory, useRouteMatch } from 'react-router';
import { useDispatch } from 'react-redux';

import useTrans from 'helper/useTrans';
import { fetchContainerTypes } from 'app/modules/Warehouse/warehouse-redux/containerTypeSlice';

import { Card, CardBody, CardHeader, CardHeaderToolbar } from '_metronic/_partials/controls';
import TableContainerTypes from 'app/modules/Warehouse/components/Table/TableContainerType';
import Button from 'app/components/Button';
import ModalCreateContainerType from './ModalCreateContainerType';
import NeedPermission from 'app/components/NeedPermission';

const ShelveManagement = props => {

    const [trans] = useTrans();
    const dispatch = useDispatch();
    const history = useHistory();
    const match = useRouteMatch();
    const modalEditRef = useRef();

    useEffect(() => {
        f5Data();
    }, []); // eslint-disable-line

    const f5Data = useCallback((params) => {
        dispatch(fetchContainerTypes({ with: "shipmentMethod;toArea", ...params }));
    }, [dispatch]);

    const handleOpenEdit = useCallback((id, containerType) => {
        modalEditRef.current.setInitialData(containerType);

        history.push(`${match.url}/edit-container-type/${id}`);
    }, [history, match.url]);

    const handleOpenModalCreate = useCallback(() => {
        history.push(`${match.url}/create-container-type`);
    }, [history, match.url]);

    return (
        <>
            <NeedPermission need={['container-types.create']}>
                <Route path={`${match.path}/create-container-type`}>
                    {({ match }) => (
                        <ModalCreateContainerType
                            show={match !== null}
                            onSuccess={f5Data}
                        />
                    )}
                </Route>
            </NeedPermission>

            <NeedPermission need={['container-types.update']}>
                <Route path={`${match.path}/edit-container-type/:id`}>
                    {({ match }) => (
                        <ModalCreateContainerType
                            ref={modalEditRef}
                            id={match?.params?.id}
                            editMode
                            show={match !== null}
                            onSuccess={f5Data}
                        />
                    )}
                </Route>
            </NeedPermission>

            <Card className="container-type-management">
                <CardHeader title={trans("warehouse.container_type.title")}>
                    <NeedPermission need={['container-types.create']}>
                        <CardHeaderToolbar>
                            <Button type="primary" onClick={handleOpenModalCreate}>{trans("warehouse.container_type.create.title")}</Button>
                        </CardHeaderToolbar>
                    </NeedPermission>
                </CardHeader>
                <CardBody>
                    <TableContainerTypes
                        onRefresh={f5Data}
                        onViewEdit={handleOpenEdit}
                    />
                </CardBody>
            </Card>
        </>
    );
};

export default ShelveManagement;