/** Data my in s nhn c */

const TEMPLATE_DATA = {
    type: "",
    pageSize: "2990",
    palletId: "undefined",
    tracking: "undefined",
    jancode: "undefined",
    sfaId: "undefined",
    quantity: "1",
    region: "Tokyo",
    sku: "undefined",
    map: "undefined",
    startIndex: "1",
    locationId: "undefined",
    floor: "undefined",
    row: "undefined",
    column: "undefined",
    createdDate: "undefined",
    itemQuantity: "undefined",
    sfaQuantity: "undefined"
}

export const getTemplateData = (type, data) => {

    console.log({
        ...applyDataToTemplate(data),
        type,
    }, 'templateData')

    return JSON.stringify({
        ...applyDataToTemplate(data),
        type,
    });
}

const applyDataToTemplate = (obj) => {
    let newObj = { ...TEMPLATE_DATA };
    Object.entries(obj).forEach(([k, v]) => {
        if (newObj[k] && v !== undefined && v !== null) newObj[k] = v.toString();
    });

    return newObj;
}

