import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { shallowEqual, useDispatch, useSelector } from 'react-redux';

import { fetchUnit } from 'app/modules/Product/product-redux/unitSlice';
import { fetchOrigin } from 'app/modules/Product/product-redux/originSlice';
import { fetchTax } from 'app/modules/Product/product-redux/taxSlice';
import {
    createProduct,
    createProductImageFile
} from 'app/modules/Product/product-redux/productSlice';

import CustomModal from 'app/components/CustomModal';
import { Button, Modal } from 'react-bootstrap';
import * as Yup from 'yup';
import { dialog } from 'app/components/DialogNotify';
import ReactTagInput from '@pathofdev/react-tag-input';
import '@pathofdev/react-tag-input/build/index.css';
import { useForm } from 'react-hook-form';
import Select from 'react-select';
import { yupResolver } from '@hookform/resolvers/yup';
import { useFileUpload } from 'use-file-upload';

import './DialogNewProduct.scss';
import useTrans from 'helper/useTrans';

const DialogNewProduct = ({ show, onHide, onSuccess }) => {
    const [tags, setTags] = useState([]);
    const [defaultSrc, setDefaultSrc] = useState(
        'https://static.thenounproject.com/png/558475-200.png'
    );

    const { unitList } = useSelector(
        ({ product }) => ({ unitList: product.unit.unitList }),
        shallowEqual
    );
    const tax = useSelector(state => state.product.tax?.taxList);
    const origin = useSelector(state => state.product.origin?.originList);

    const dispatch = useDispatch();
    const [trans] = useTrans();

    const validationSchema = Yup.object().shape({
        id: Yup.string()
            .max(13, trans("validation.message.max_string", { max: 13 }))
            .required(trans("validation.message.required")),
        name: Yup.string().required(trans("validation.message.required")),
        price: Yup.number().required(trans("validation.message.required"))
    });

    const { register, handleSubmit, errors, reset, setValue } = useForm({
        resolver: yupResolver(validationSchema),
    });
    const [files, selectFiles] = useFileUpload();

    useEffect(() => {
        dispatch(fetchOrigin());
        dispatch(fetchUnit());
        dispatch(fetchTax());
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    const taxSelect = tax.map(item => ({
        value: item?.id,
        label: item?.name
    }));
    const originSelect = origin.map(item => ({
        value: item?.id,
        label: item?.name
    }));
    const unitSelect = unitList.map(item => ({
        value: item?.id,
        label: item?.name
    }));

    const handleUploadImage = e => {
        const urlImage = e.target.value;
        if (urlImage.includes('data:')) {
            dialog.error(trans("validation.message.not_found.url"));
        } else {
            setDefaultSrc(urlImage);
        }
    };

    const resetForm = () => {
        reset({
            id: '',
            name: '',
            price: '',
            origin_id: '',
            unit_id: '',
            ingredients: [],
            tax_id: '',
        });

        setDefaultSrc('https://static.thenounproject.com/png/558475-200.png');
        setTags([]);
    }

    const onSubmit = data => {
        const params = {
            id: data.id || '',
            name: data.name || '',
            price: data.price || '',
            origin_id: data.origin_id || '',
            unit_id: data.unit_id || '',
            ingredients: JSON.stringify(data.tags || []),
            tax_id: data.tax_id || '',
            image_url: defaultSrc,
            package: JSON.stringify({
                quantity: data.quantity,
                height: data.height,
                weight: data.weight,
                length: data.length,
                width: data.width
            })
        };

        if (files != null) {
            const formData = new FormData();
            formData.set('image', files.file, files.name);

            const body = {
                params: params,
                data: formData
            };
            createProductWithImg(body);
        } else {
            createProductFunc(params);
        }
    };

    const createProductWithImg = (body) => {
        dispatch(createProductImageFile(body)).then(res => {
            if (res.type.includes('fulfilled')) {
                dialog.success(trans("warehouse.jancode.create.success"));
                onSuccess && onSuccess(res.payload);
                resetForm();
            } else {
                dialog.error(trans("warehouse.jancode.create.success"));
            }
        });
    }

    const createProductFunc = (params) => {
        dispatch(createProduct(params)).then(res => {
            if (res.type.includes('fulfilled')) {
                dialog.success(trans("warehouse.jancode.create.success"));
                onSuccess && onSuccess(res.payload);
                resetForm();
            } else {
                dialog.error(trans("warehouse.jancode.create.failure"));
            }
        });
    }

    return (
        <CustomModal
            title={trans("warehouse.jancode.create.title")}
            onHide={onHide}
            actionsLoading={false}
            show={show}
            className="dialog-add-new-product"
        >
            <Modal.Body>
                <div className="row">
                    <div className="col-lg-5 col-md-4 app-image">
                        <button
                            onClick={() =>
                                selectFiles({
                                    accept: 'image/*'
                                })
                            }
                            type="button"
                            data-toggle="tooltip"
                            data-placement="top"
                            title={trans("warehouse.jancode.image.click_to_change")}
                        >
                            <img
                                src={files?.source || defaultSrc}
                                alt="preview"
                            />
                        </button>
                        <div className="input-group mt-3">
                            <div className="input-group-prepend">
                                <span
                                    className="input-group-text"
                                    id="basic-addon3"
                                    style={{
                                        backgroundColor:
                                            '#3699FF',
                                        color: '#ffffff'
                                    }}
                                >
                                    {trans("warehouse.jancode.image.url")}
                                </span>
                            </div>
                            <input
                                type="text"
                                className="form-control"
                                id="basic-url"
                                aria-describedby="basic-addon3"
                                onChange={handleUploadImage}
                            />
                        </div>
                    </div>
                    <div className="col-lg-7 col-md-8 pl-10">
                        <form onSubmit={handleSubmit(onSubmit)}>
                            <div className="form-group row align-items-center">
                                <div className="col-3">
                                    <span className="order-title">
                                        {trans("warehouse.jancode.id")}
                                    </span>
                                </div>
                                <div className="col-9">
                                    <input
                                        className="form-control"
                                        name="id"
                                        ref={register}
                                    />
                                    {errors.id && (
                                        <p
                                            style={{
                                                color: 'red',
                                                margin: 'unset'
                                            }}
                                        >
                                            {errors.id.message}
                                        </p>
                                    )}
                                </div>
                            </div>
                            <div className="form-group row align-items-center">
                                <div className="col-3">
                                    <span className="order-title">
                                        {trans("common.name")}
                                    </span>
                                </div>
                                <div className="col-9">
                                    <input
                                        className="form-control"
                                        name="name"
                                        ref={register}
                                    />
                                    {errors.name && (
                                        <p
                                            style={{
                                                color: 'red',
                                                margin: 'unset'
                                            }}
                                        >
                                            {errors.name.message}
                                        </p>
                                    )}
                                </div>
                            </div>
                            <div className="form-group row align-items-center">
                                <div className="col-3">
                                    <span className="order-title">
                                        {trans("common.price")}
                                                </span>
                                </div>
                                <div className="col-9">
                                    <input
                                        type="number"
                                        min="1"
                                        className="form-control"
                                        name="price"
                                        ref={register}
                                    />
                                    {errors.price && (
                                        <p
                                            style={{
                                                color: 'red',
                                                margin: 'unset'
                                            }}
                                        >
                                            {errors.price.message}
                                        </p>
                                    )}
                                </div>
                            </div>
                            <div className="form-group row align-items-center">
                                <div className="col-3">
                                    <span className="order-title">
                                        {trans("warehouse.jancode.origin.title")}
                                                </span>
                                </div>
                                <div className="col-9">
                                    <Select
                                        ref={() => register('origin_id')}
                                        name="origin_id"
                                        defaultValue={''}
                                        options={originSelect}
                                        onChange={value =>
                                            setValue('origin_id', value.value)
                                        }
                                        placeholder={trans("warehouse.jancode.origin.select_title")}
                                    />
                                    {errors.origin_id && (
                                        <p>
                                            {trans("validation.message.required")}
                                        </p>
                                    )}
                                </div>
                            </div>
                            <div className="form-group row align-items-center">
                                <div className="col-3">
                                    <span className="order-title">
                                        {trans("warehouse.jancode.unit.title")}
                                    </span>
                                </div>
                                <div className="col-9">
                                    <Select
                                        ref={() => register('unit_id')}
                                        name="unit_id"
                                        defaultValue={''}
                                        options={unitSelect}
                                        onChange={value =>
                                            setValue('unit_id', value.value)
                                        }
                                        placeholder={trans("warehouse.jancode.unit.select_title")}
                                    />
                                    {errors.exampleRequired && (
                                        <p>
                                            {trans("validation.message.required")}
                                        </p>
                                    )}
                                </div>
                            </div>
                            <div className="form-group row align-items-center">
                                <div className="col-3">
                                    <span className="order-title">
                                        {trans("warehouse.jancode.tax.title")}
                                    </span>
                                </div>
                                <div className="col-9">
                                    <Select
                                        ref={() => register('tax_id')}
                                        name="tax_id"
                                        defaultValue={''}
                                        options={taxSelect}
                                        onChange={value =>
                                            setValue('tax_id', value.value)
                                        }
                                        placeholder={trans("warehouse.jancode.tax.select_title")}
                                    />
                                    {errors.exampleRequired && (
                                        <p>
                                            {trans("validation.message.required")}
                                        </p>
                                    )}
                                </div>
                            </div>
                            <div className="form-group row align-items-center">
                                <div className="col-3">
                                    <span className="order-title">
                                        {trans("warehouse.jancode.properties.title")}
                                    </span>
                                </div>
                                <div className="col-9">
                                    <ReactTagInput
                                        ref={() => register('tags')}
                                        className="form-control "
                                        tags={tags}
                                        placeholder={trans("warehouse.jancode.properties.title")}
                                        editable={true}
                                        readOnly={false}
                                        onChange={newTags => {
                                            setValue('tags', newTags)
                                            setTags(newTags)
                                        }}
                                        allowDuplicates={false}
                                    />
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </Modal.Body>
            <Modal.Footer>
                <Button variant="secondary" onClick={onHide}>{trans("common.cancel")}</Button>
                <Button variant="primary" onClick={handleSubmit(onSubmit)}>{trans("common.save")}</Button>
            </Modal.Footer>
        </CustomModal>
    );
};

DialogNewProduct.propTypes = {
    onHide: PropTypes.func,
    show: PropTypes.bool,
    onSuccess: PropTypes.func
};

export default DialogNewProduct;