import React, { useCallback } from 'react';
import PropTypes from 'prop-types';
import { useDispatch, useSelector } from 'react-redux';

import { fetchContainerTypes } from 'app/modules/Warehouse/warehouse-redux/containerTypeSlice';
import useTrans from 'helper/useTrans';

import TMNBaseSelect from '../BaseSelect';

import './index.scss';

const SelectContainerType = ({ onChange, value, showLabel = true, ...props }) => {
    const containerTypeList = useSelector(state => state.warehouse.containerType.list.data);
    const loading = useSelector(state => state.warehouse.containerType.list.loading);
    const dispatch = useDispatch();
    const [trans] = useTrans();

    const makeOption = useCallback((item) => ({
        value: item.id,
        label: trans("warehouse.container_type.option_label", {
            id: item.id,
            shipment_method_name: item?.shipment_method?.name || '-',
            volume: item.volume || 0,
            day_of_week: item.day_of_week === 1 ? 'Ch nht' : `Th ${item.day_of_week}`
        })
    }), [trans]);

    const containerTypeOptions = containerTypeList?.map(item => makeOption(item));

    const fetchData = (params) => {
        dispatch(fetchContainerTypes({ with: 'shipmentMethod', ...params }));
    }

    const handleSelectContainerType = option => {
        onChange && onChange(option.value);
    };

    return (
        <TMNBaseSelect
            label={showLabel && trans("warehouse.container_type.title")}
            value={value}
            placeholder={trans("common.select_here")}
            options={containerTypeOptions}
            onChange={handleSelectContainerType}
            loading={loading}
            className="select-container-type"
            onFetchData={fetchData}
            {...props}
        />
    );
};

SelectContainerType.propTypes = {
    onChange: PropTypes.func,
    value: PropTypes.any,
    showLabel: PropTypes.bool
};

export default SelectContainerType;
