import React, { useRef, useState } from 'react';
import { useSelector } from 'react-redux';
import { useHistory } from 'react-router';

import { startPrinter } from 'app/components/PrinterModal';
import { getTemplateData } from 'helper/printerTemplateData';
import handleApiError from 'helper/handleApiError';
import warehouseApi from 'apis/warehouse';
import useTrans from 'helper/useTrans';

import CustomModal from 'app/components/CustomModal';
import { Modal } from 'react-bootstrap';
import FormAddLocation from 'app/modules/Warehouse/components/Form/FormAddLocation';
import Button from 'app/components/Button';

const ModalAddLocation = ({ show, onHide, onSuccess }) => {

    const [loading, setLoading] = useState();
    const areaList = useSelector(state => state.warehouse.area.list);
    const history = useHistory();
    const formRef = useRef();
    const [trans] = useTrans();

    const handleSaveLocation = (values, form) => {
        setLoading(true);
        warehouseApi.location.create(values)
            .then((res) => {
                startPrinter(getTemplateData('LOCATION', {
                    locationId: res.id,
                    floor: res.floor,
                    row: res.row,
                    column: res.column,
                    quantity: 2
                }));

                onSuccess && onSuccess(res);
                closeModal();
            })
            .catch((err) => {
                console.error(err);
                handleApiError(err, form);
            })
            .finally(() => {
                setLoading(false);
            })
    }

    const closeModal = () => {
        if (onHide) onHide();
        else history.goBack();
    }

    const triggerSubmit = () => {
        formRef.current.submitForm();
    }

    return (
        <CustomModal
            show={show}
            onHide={closeModal}
            title={trans("warehouse.location.create.title")}
            actionsLoading={loading}
        >
            <Modal.Body>
                <FormAddLocation
                    initialValues={{
                        area_id: areaList?.[0]?.id,
                        shelve_id: '',
                        floor: 1,
                        row: 1,
                        column: 1
                    }}
                    onSubmit={handleSaveLocation}
                    ref={formRef}
                />
            </Modal.Body>
            <Modal.Footer>
                <Button type="secondary" loading={loading} onClick={closeModal} className="btn-large">{trans("common.back")}</Button>
                <Button type="primary" loading={loading} onClick={triggerSubmit} className="btn-large">{trans("common.save")}</Button>
            </Modal.Footer>
        </CustomModal>
    );
};

ModalAddLocation.propTypes = {

};

export default ModalAddLocation;