import React, { useState } from 'react';

import useTrans from 'helper/useTrans';

import { Input, BarcodeInput } from '_metronic/_partials/controls';
import Button from 'app/components/Button';

const TrackingInput = props => {

    const { form, field, disabled } = props;
    const [isScanTracking, setIsScanTracking] = useState(true);
    const [trans] = useTrans();

    const toggleIsScanTracking = () => setIsScanTracking(!isScanTracking);

    const clickToggle = (e) => {
        e.target.blur();
        form.setFieldValue(field.name, '');

        toggleIsScanTracking();
    }

    if (isScanTracking) {
        return (
            <div className="tracking-input barcode-input">
                <BarcodeInput {...props} canTypeOnInput={false} />

                <Button type="primary" onClick={clickToggle} disabled={disabled}>
                    {trans("warehouse.tracking.type_tracking")}
                </Button>
            </div>
        )
    }

    return (
        <div className="tracking-input normal-input">
            <Input {...props} />

            <Button type="primary" onClick={clickToggle} disabled={disabled}>
                {trans("warehouse.tracking.scan_tracking")}
            </Button>
        </div>
    );
};

TrackingInput.propTypes = {

};

export default TrackingInput;