import React, { useMemo } from 'react';
import PropTypes from 'prop-types';

import { toAbsoluteUrl } from '_metronic/_helpers';
import clsx from 'clsx';
import formatNumber from 'helper/formatNumber';
import { currenyUnit } from 'config/currency';
import useTrans from 'helper/useTrans';

import SVG from 'react-inlinesvg';
import { Card, CardBody } from '_metronic/_partials/controls';
import { Divider } from '@material-ui/core';
import CustomTable from 'app/components/CustomTable';

import './InboundPlanItem.scss';

const productItemColumns = [
    { id: 'id', title: <span>Jancode</span> },
    { id: 'quantity', title: <span>S lng</span>, render: (quantity) => formatNumber(quantity) },
    { id: 'order_id', title: <span>M t hng</span> },
    { id: 'amount', title: <span>Tng tin</span>, render: (amount) => `${formatNumber(amount)} ${currenyUnit}` },
    {
        id: 'is_box', title: <span>ng gi</span>, render: (is_box) => {
            if (is_box) return <span className="isbox-badge packbox">C</span>
            return <span className="isbox-badge unpackbox">Khng</span>
        }
    },
]

const InboundPlanItem = ({ data }) => {

    const [trans] = useTrans();

    const iconUrl = useMemo(() => {
        if (data.checked) return '/media/svg/icons/Shopping/delivery-truck-shipped.svg';

        return '/media/svg/icons/Shopping/delivery-truck.svg';
    }, [data.checked]);

    if(!data?.code) return <></>;

    return (
        <Card className="inbound-plan-item position-relative">
            <CardBody>
                <div className="info-section">
                    <div className="content-wrapper">
                        <div className={clsx(data?.checked ? 'received' : 'pending', 'is-checked custom-box status-box')}>
                            {data?.checked ?
                                trans("warehouse.inbound_plan.arive_status.arrived") :
                                trans("warehouse.inbound_plan.arive_status.not_yet")
                            }
                        </div>
                        <div className="code custom-box">
                            <div className="code-label">{trans("warehouse.tracking.id")}</div>
                            <div className="code-value">{data?.code}</div>
                        </div>
                    </div>

                    <div className={clsx("icon-wrapper", !data?.checked && "not_yet")}>
                        <SVG src={toAbsoluteUrl(iconUrl)} />
                    </div>
                </div>

                {data?.order_items?.length ? <Divider /> : ''}

                <div className="items-wrapper">
                    {data?.order_items?.length ? (
                        <CustomTable
                            columns={productItemColumns}
                            rows={data?.order_items || []}
                            isAction={false}
                            className="table-tracking-products"
                            isPagination={false}
                        />
                    ) : ''}
                </div>
            </CardBody>
        </Card>
    );
};

InboundPlanItem.propTypes = {
    data: PropTypes.any
};

export default InboundPlanItem;