import React, { useCallback, useEffect, useState } from 'react';

import warehouseApi from 'apis/warehouse';
import moment from 'moment';

import { Card, CardBody } from '_metronic/_partials/controls';
import Loading from 'app/components/Loading';

const momentFormatter = "YYYY-MM-DD"

const SFACreatedToday = props => {

    const [total, setTotal] = useState(0);
    const [loading, setLoading] = useState(false);

    useEffect(() => {
        getDataSFA();
    }, []); // eslint-disable-line

    const getDataSFA = useCallback(() => {
        setLoading(true);
        return warehouseApi.SFA.fetchSFAs({
            search: `created_at:${moment().format(momentFormatter)}`,
            searchFields: `created_at:like`
        }).then(res => {
            setTotal(res.total);
        }).finally(() => {
            setLoading(false);
        })
    }, []); // eslint-disable-line

    return (
        <Card className="sfa-created-today dashboard-info-box wave wave-animate wave-primary">
            <CardBody>
                {loading && <Loading local />}
                <div className="title">SFA  to hm nay</div>
                <div className="total">{total}</div>
            </CardBody>
        </Card>
    );
};

export default SFACreatedToday;