import React from 'react';

import { DatePickerField } from '_metronic/_partials/controls';
import { Form, Formik } from 'formik';
import * as Yup from 'yup';
import FormStatus from '../FormStatus';
import SelectContainerTypeForm from 'app/components/Select/SelectContainerType/SelectContainerTypeForm';

import './index.scss';
import useTrans from 'helper/useTrans';

const FormAddInvoice = ({
    isEdit = true,
    isShowCutOff = false,
    initialValues = {
        shipping_date: new Date(),
        type_id: ""
    },
    onSubmit
}, ref) => {

    const [trans] = useTrans();

    const validationSchema = Yup.object().shape({
        type_id: Yup.string().required(trans("validation.message.required")),
        shipping_date: Yup.date().required(trans("validation.message.required"))
    });

    return (
        <Formik
            onSubmit={onSubmit}
            initialValues={initialValues}
            validationSchema={validationSchema}
            enableReinitialize
            innerRef={ref}
        >
            <Form className="form form-label-right form-add-invoce">
                <div className="row">
                    <div className="col-lg-6">
                        <SelectContainerTypeForm name="type_id" isDisabled={!isEdit} />
                    </div>
                    <div className="col-lg-6">
                        <DatePickerField
                            name="shipping_date"
                            label={trans("warehouse.invoice.shipping_date")}
                            placeholder={trans("warehouse.invoice.shipping_date")}
                            disabled={!isEdit}
                        />
                    </div>
                    <div className="col-lg-6">
                        <DatePickerField
                            name="vanning_date"
                            label={trans("warehouse.invoice.vanning_date")}
                            placeholder={trans("warehouse.invoice.vanning_date")}
                            disabled={!isEdit}
                        />
                    </div>
                    <div className="col-lg-6">
                        <DatePickerField
                            name="departure_date"
                            label={trans("warehouse.invoice.departure_date")}
                            placeholder={trans("warehouse.invoice.departure_date")}
                            disabled={!isEdit}
                        />
                    </div>
                    <div className="col-lg-6">
                        <DatePickerField
                            name="arrival_date"
                            label={trans("warehouse.invoice.arrival_date")}
                            placeholder={trans("warehouse.invoice.arrival_date")}
                            disabled={!isEdit}
                        />
                    </div>
                    {isShowCutOff && (
                        <div className="col-lg-6">
                            <DatePickerField
                                name="cut_off_date"
                                label={trans("warehouse.invoice.cut_off_date")}
                                placeholder={trans("warehouse.invoice.cut_off_date")}
                                disabled={true}
                            />
                        </div>
                    )}
                </div>

                <FormStatus />
            </Form>
        </Formik>
    );
};

export default React.forwardRef(FormAddInvoice);
