import React from 'react';

import clsx from 'clsx';
import { useField, useFormikContext } from "formik";
import SelectCustomerForm from 'app/components/Select/SelectCustomer/SelectCustomerForm';
import useTrans from 'helper/useTrans';

const SelectObjValue = ({ name, showLabel = true, ...props }) => {

    const [field, meta] = useField(name);
    const { setFieldValue, values } = useFormikContext();
    const { error, touched } = meta;
    const [trans] = useTrans();

    const handleChangeCustomer = (data) => {
        setFieldValue(field.name, data);
    }

    if(values?.objectable_type === 'user')
        return <SelectCustomerForm onChange={handleChangeCustomer} name={field.name} />

    return (
        <div className={clsx(touched && error && 'is-invalid')}>
            <div className="select-customer">
                <div className="form-group">
                    {showLabel && <label htmlFor="">{trans("warehouse.order.owner_type.order")}</label>}
                    <input {...field} className="form-control" />
                </div>
            </div>

            {touched && error && (
                <div className="invalid-feedback d-block">{error}</div>
            )}
        </div>
    );
};

SelectObjValue.propTypes = {

};

export default SelectObjValue;